import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {questionActionTypes} from '../sn-help-question';

const view = ({properties}) => {
	if(!Array.isArray(properties.questionsConfig))
		return;

	return properties.questionsConfig.map(questionConfig => <sn-help-question {...questionConfig}/>);
};

/**
 * 	@summary Renders multiple questions with choices based on props. Uses sn-help-question component to
 * 	display each question
 *	@seismic sn-help-question-list
 */
createCustomElement('sn-help-question-list', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		/**
		 * An array of config related to question. For each question config, Please refer to sn-help-question component
		 */
		questionsConfig : {
			required: true,
			schema: {
				type: 'array',
				maxLength: 5,
				questionsConfig: {
					type: 'array',
					properties: {
						answers: {type: 'array'},
						id: {type: 'string'},
						question: {type: 'string'},
						type: {type: 'string', enum: ['MULTIPLE', 'SINGLE']},
						required: {type: 'boolean'},
						variant: {type: 'string', enum: ['CHECKBOX', 'DROPDOWN', 'RADIO', 'TEXT']}
					}
				}
			}
		}
	},
	dispatches: {
		/**
         *	Dispatched when user selects/type any choice/value.
         *	@type {{id: string, values: array}}
         *	@valueType {{name: string, value: string}}
         *	@action	SN_HELP_QUESTION#VALUES_SET
         */
		[questionActionTypes.QUESTION_VALUES_SET]: {}
	}
});
