import { snHelpActionTypes } from './constants';
import {observable} from './observable';

const messageQueue = observable.getInstance();

export const snHelpBehavior = {
	name: 'sn-help-behavior',
	actionHandlers: {
		/**
		 * Handles action SN_HELP#GUIDANCE_START_REQUESTED emitted by app
		 * @param helpers
		 */
		[snHelpActionTypes.GUIDANCE_START_REQUESTED] : helpers => messageQueue.trigger(snHelpActionTypes.GUIDANCE_START_REQUESTED, helpers),

		/**
		 * Handles action SN_HELP#GUIDANCE_STOP_REQUESTED emitted by app
		 * @param helpers
		 */
		[snHelpActionTypes.GUIDANCE_STOP_REQUESTED] : helpers => messageQueue.trigger(snHelpActionTypes.GUIDANCE_STOP_REQUESTED, helpers),

		/**
		 * Listens to SN_HELP#ADD_NODE_REQUESTED emitted by app
		 * @param helpers
		 */
		[snHelpActionTypes.GUIDANCE_ADD_NODE_REQUESTED] : helpers => messageQueue.trigger(snHelpActionTypes.GUIDANCE_ADD_NODE_REQUESTED, helpers),

		/**
		 * Listens to SN_HELP#REMOVE_NODE_REQUESTED emitted by app
		 * @param helpers
		 */
		[snHelpActionTypes.GUIDANCE_REMOVE_NODE_REQUESTED] : helpers => messageQueue.trigger(snHelpActionTypes.GUIDANCE_REMOVE_NODE_REQUESTED, helpers),

		/**
		 * Listens to SN_HELP#CLEAR_NODES_REQUESTED emitted by app
		 * @param helpers
		 */
		[snHelpActionTypes.GUIDANCE_CLEAR_NODES_REQUESTED] : helpers => messageQueue.trigger(snHelpActionTypes.GUIDANCE_CLEAR_NODES_REQUESTED, helpers),
	}
};
