import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {view} from './view';
import {stepOrchestratorInternalActionHandlers} from './actionHandlers/coreActions';
import {coreActions, fetchStatus} from './constants';
import {stepOrchestratorExternalActionHandlers} from './actionHandlers/guidanceHandlers';


/**
 * 	Help Guidance Component
 *
 * 	A connected component which takes care of single guidance.
 * 	Based on guidance step type, this component will select the suitable
 * 	component and pass the guidance step config.
 *
 * 	@summary A connected component to handle single guidance
 * 	@seismicElement sn-help-step-orchestrator
 */

createCustomElement('sn-help-step-orchestrator', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		guidanceId: {
			default: null,
			required: false,
			schema: {
				type: 'string'
			}
		},
		guidanceElemRegistry: {
			default: null,
			required: false
		}
	},
	initialState: {
		interactionId: -1,
		guidanceType: null,
		fetchStatus: fetchStatus.READY,
		currentStep: null,
		steps: [],
		totalSteps: null,
		answers: {}
	},
	actionHandlers: {
		...stepOrchestratorExternalActionHandlers,
		...stepOrchestratorInternalActionHandlers
	},
	dispatches: {
		/**
		 * 	GUIDANCE ACTIONS
		 */

		/**
		 *	Dispatches when the guidance started
		 *	@actionType SN_HELP_STEP_ORCHESTRATOR#STARTED
		 */
		[coreActions.GUIDANCE_STARTED]: {},

		/**
		 *	Dispatches when the guidance ended
		 *	@actionType SN_HELP_STEP_ORCHESTRATOR#ENDED
		 */
		[coreActions.GUIDANCE_ENDED]: {},

		/**
		 *	Dispatches when the guidance cancelled
		 *	@actionType SN_HELP_STEP_ORCHESTRATOR#CANCELLED
		 */
		[coreActions.GUIDANCE_CANCELLED]: {},

		/**
		 * 	GUIDANCE STEP ACTIONS
		 */

		/**
		 *	Dispatches when the guidance step started
		 *	@actionType SN_HELP_STEP_ORCHESTRATOR#STEP_STARTED
		 */
		[coreActions.GUIDANCE_STEP_STARTED]: {},

		/**
		 *	Dispatches when the guidance step ended
		 *	@actionType SN_HELP_STEP_ORCHESTRATOR#STEP_STARTED
		 */
		[coreActions.GUIDANCE_STEP_ENDED]: {},

		/**
		 *	Dispatches when user clicks and footer action
		 *	@actionType SN_HELP_STEP_ORCHESTRATOR#STEP_ACTION_TAKEN
		 */
		[coreActions.GUIDANCE_STEP_ACTION_TAKEN]: {}
	}
});
