import {modalActions} from '../../../presentational/sn-help-modal';
import {questionActionTypes} from '../../../presentational/sn-help-question';
import {
	internalActions
} from '../constants';
import {calloutActions} from '../../../presentational/sn-help-callout/constants';


/**
 * Listens to all actions dispatched by child components and takes action accordingly
 */
export const stepOrchestratorExternalActionHandlers = {

	/**
	 * sn-help-modal actions
	 */

	/**
	 * Executes when any footer action is clicked in sn-help-modal
	 */
	[modalActions.MODAL_FOOTER_ACTION_CLICKED]({action, dispatch}) {
		dispatch(internalActions.GUIDANCE_FOOTER_ACTION_CLICKED, action.payload);
	},

	/**
	 * Executes when sn-help-modal is closed through ESC or Close icon
	 */
	[modalActions.MODAL_CLOSED]({dispatch}) {
		dispatch(internalActions.GUIDANCE_CLOSED);
	},

	/**
	 * sn-help-question actions
	 */

	/**
	 * Executes when user selects any answer
	 */
	[questionActionTypes.QUESTION_VALUES_SET]({state, action, updateState}) {
		const {payload} = action;

		updateState({
			answers: {
				...state.answers,
				[payload.id] : payload.values
			}
		});
	},

	/**
	 * sn-help-callout actions
	 */

	/**
	 * Executes when any footer action is clicked in sn-help-callout
	 * @param action
	 * @param dispatch
	 */
	[calloutActions.CALLOUT_FOOTER_ACTION_CLICKED]({action, dispatch}) {
		dispatch(internalActions.GUIDANCE_FOOTER_ACTION_CLICKED, action.payload);
	},

	/**
	 * Executes when sn-help-modal is closed.
	 * @param dispatch
	 */
	[calloutActions.CALLOUT_CLOSED]({dispatch}) {
		dispatch(internalActions.GUIDANCE_CLOSED);
	}
};
