import {fetchStatus, guidanceTypes} from './constants';
import '../../presentational/sn-help-modal';
import {getFooterActions} from './utils';
import {footerActionTypes} from '../../../sn-help/constants';
import '../../presentational/sn-help-callout';

export const view = state => {
	if(state.properties.guidanceId === null || state.fetchStatus !== fetchStatus.DONE || state.currentStep === null)
		return null;

	const currentStepConfig = state.steps[state.currentStep];
	if(!currentStepConfig)
		return null;

	currentStepConfig.footerActions = getFooterActions(currentStepConfig.actions_label, state.steps.length, state.currentStep);
	currentStepConfig.stepProgressConfig = {
		showStepProgress: true,
		totalSteps: state.steps.length,
		currentStep: state.currentStep+1
	};

	return state.guidanceType === guidanceTypes.MODAL ? getModal(currentStepConfig, state) : getCallout(currentStepConfig, state.properties.guidanceElemRegistry);
};

const getModal = (config, state) => {
	const stepQuestions = config.content && config.content.questions;

	//Disable 'NEXT' footer action if question is mandatory and user not picked any answer
	if(stepQuestions && stepQuestions.length > 0) {
		const disableNext = stepQuestions.some(questionConfig => {
			if(questionConfig.mandatory === 'true' && (!state.answers[questionConfig.id] || state.answers[questionConfig.id].length===0))
				return true;
			return false;
		});
		if(disableNext) {
			const disableFooterActions = config.footerActions.filter(footerConfig => footerConfig.value === footerActionTypes.NEXT || footerConfig.value === footerActionTypes.FINISH);
			disableFooterActions.forEach(footerAction => footerAction.disabled = 'true');
		}
	}

	return <sn-help-modal {...config}/>;
};

const getCallout = (config, guidanceElemRegistry) => {
	const {target_element, footerActions} = config;
	const {title, text, has_custom_html, custom_html} = config.content;
	return <sn-help-callout
		targetRef={guidanceElemRegistry.get(target_element)}
		heading={title} content={has_custom_html === 'true' ? custom_html : text}
		footerActions={footerActions}
	/>;
};
