import '@servicenow/now-checkbox';
import '@servicenow/now-radio-buttons';
import '@servicenow/now-rich-text';
import {modalLayouts} from './constants';
import '../sn-help-question-list';

//renders html content using now-rich-text NDS component
export const renderHtmlContent = (content, layout) => {
	if(!content)
		return null;

	let customHTML;

	if(content.has_custom_html === 'true')
		customHTML = content.custom_html;
	else if(layout === modalLayouts.TEXT)
		customHTML = content.text;
	else
		customHTML = content;

	return customHTML ? <now-rich-text html={customHTML}/> : null;
};

//render image
const renderImageContent = content => {
	let imageSource;

	if(content.has_image_link === 'true')
		imageSource = content.image_link;
	else if(content.image)
		imageSource = content.image;

	const media = imageSource ? <img src={imageSource} alt={content.image_alt} /> : null;
	return <div className="sn-help-modal-media">{media}</div>;
};

//render video content for modal
const renderVideoContent = (content, layout) => {

	const media = content.video_link ? <video controls preload="">
		<source src={content.video_link} type={'video/mp4'}/>
		{content.video_alt}
	</video>: renderHtmlContent(content.custom_html, layout);

	return <div className="sn-help-modal-media">{media}</div>;
};

//render QUESTION layout for Modal
const renderQuestionContent = content => {
	const {questions} = content;
	if(!questions)
		return null;

	// Need to convert sys_id to id as NDS components expect in that format
	questions.forEach(question => {
		question.id = question.sys_id;
		question.answers && question.answers.forEach(answer => answer.id = answer.sys_id);
	});
	return <sn-help-question-list questionsConfig={questions}/>;
};

//getclassName for content Image/Video
const getClassNameForContent = contentType => (contentType === modalLayouts.IMAGE || contentType === modalLayouts.VIDEO) ? '-content' : '';

//render Two column layout for modal
const renderTwoColumnLayout = (content, layout) => {
	const colContent = layout.split('_');

	return <div className="sn-help-modal-two-column">
		<div className={`sn-help-modal-two-column -left ${getClassNameForContent(colContent[0])}`}>
			{renderModalByLayout[colContent[0]](content, colContent[0])}
		</div>
		<div className={`sn-help-modal-two-column -right ${getClassNameForContent(colContent[1])}`}>
			{renderModalByLayout[colContent[1]](content, colContent[1])}
		</div>
	</div>;
};

//render modal based on specific layout
export const renderModalByLayout = {
	[modalLayouts.IMAGE]: renderImageContent,
	[modalLayouts.VIDEO]: renderVideoContent,
	[modalLayouts.TEXT]: renderHtmlContent,
	[modalLayouts.IMAGE_TEXT]: renderTwoColumnLayout,
	[modalLayouts.TEXT_IMAGE]: renderTwoColumnLayout,
	[modalLayouts.VIDEO_TEXT]: renderTwoColumnLayout,
	[modalLayouts.TEXT_VIDEO]: renderTwoColumnLayout,
	[modalLayouts.QUESTION]: renderQuestionContent,
	[modalLayouts.IMAGE_QUESTION]: renderTwoColumnLayout,
	[modalLayouts.QUESTION_IMAGE]: renderTwoColumnLayout
};
