import '@servicenow/now-modal';
import {modalLayouts} from './constants';
import {renderModalByLayout, renderHtmlContent} from './renderContent';
import '../sn-help-step-progress';


const renderModal = (layout, footerActions, content, stepProgressConfig) => {
	const {showStepProgress, totalSteps, currentStep} = stepProgressConfig;
	const contentClass = `sn-help-modal-content ${showStepProgress ? 'show-progress' : ''}`;

	return <now-modal
		opened={true}
		size='lg'
		contentFullWidth={true}
		footer-actions={footerActions}
		header-label={content.title}>
		<div className="sn-help-modal-wrapper">
			<div className={contentClass}>
				{renderHtmlContent(content.subtitle)}
				{renderModalByLayout[layout](content, layout)}
				{renderHtmlContent(content.footer)}
			</div>
			{showStepProgress ? <sn-help-step-progress totalSteps={totalSteps} currentStep={currentStep}/> : '' }
		</div>
	</now-modal>;
};

export const view = state => {
	const {layout, footerActions, content, stepProgressConfig} = state.properties;
	if(Object.values(modalLayouts).indexOf(layout) === -1)
		return null;

	return renderModal(layout, footerActions, content, stepProgressConfig);
};
