import {footerActionTypes} from '../../../sn-help/constants';

export const getFooterActions = (footerConfig, stepsLength, currentStep) => {
	if(!footerConfig)
		return [];

	const footerActionsObj = JSON.parse(footerConfig);
	const updatedFooterActionsObj = {};

	if(currentStep !== 0)
		updatedFooterActionsObj[footerActionTypes.PREVIOUS] = footerActionsObj[footerActionTypes.PREVIOUS];

	if(currentStep === stepsLength-1)
		updatedFooterActionsObj[footerActionTypes.FINISH] = footerActionsObj[footerActionTypes.FINISH];
	else
		updatedFooterActionsObj[footerActionTypes.NEXT] = footerActionsObj[footerActionTypes.NEXT];


	Object.keys(updatedFooterActionsObj).forEach(action => {
		const footerAction = {
			label: updatedFooterActionsObj[action],
			variant: 'primary',
			value: action
		};

		if(action !== footerActionTypes.NEXT && action !== footerActionTypes.FINISH)
			footerAction.variant = 'secondary';

		updatedFooterActionsObj[action] = footerAction;
	});

	const footerActions = [];
	const footerActionsOrder = [footerActionTypes.FINISH, footerActionTypes.NEXT, footerActionTypes.PREVIOUS, footerActionTypes.SKIP];
	footerActionsOrder.forEach(action => {
		if (updatedFooterActionsObj[action])
			footerActions.push(updatedFooterActionsObj[action]);
	});

	return footerActions;
};
