import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {calloutActions} from './constants';
import {calloutActionHandlers} from './actionHandlers';
import {view} from './view.js';

/**
 * sn-help-callout provides the ability to show arbitrary content that floats above
 * other elements on the page.
 * @seismicElement sn-help-callout
 * @summary A container that floats its contents over other elements on the page.
 */
createCustomElement('sn-help-callout', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		/**
        * The element that the content should be positioned to. This can be used
        * as an alternative to slotting in the trigger node, which can be useful
        * when the trigger element changes dynamically. However, in most cases
        * using a trigger slot should be preferred.
        *
        * @type {HTMLElement}
        * @private
        **/
		targetRef: {type: 'string'},

		/**
        * An array of objects that defines the buttons in the footer of the sn-help-callout.
        * You can define as many as two buttons. The buttons are laid out from right to left.
        * The keys of each object are properties for `now-button`. See `now-button`
        * for more documentation on these properties.
        * @type {{ label: string, variant: string, disabled: boolean, clickActionType}[]}
        */
		footerActions: {
			schema: {
				type: 'array',
				items: {
					type: 'object',
					properties: {
						label: {type: 'string'},
						variant: {type: 'string'},
						disabled: {type: 'boolean'},
						clickActionType: {type: 'string'}
					}
				}
			}
		},

		/**
        * Specify content to render under sn-help-callout
        * @type String
        */
		content: {type: 'string'},

		/**
        *specify header label for sn-help-callout
        *@type string
        */
		heading: {type: 'string', default: ''}

	},
	initialState:{
		/**
        * Automatically updates when the user opens or closes the callout.
        *  @type {{value: boolean}}
        **/
		opened: true
	},
	actionHandlers: calloutActionHandlers,
	dispatches: {
		/**
        * Dispatches when the sn-help-callout is closed
        * @actionType SN_HELP_CALLOUT#CALLOUT_CLOSED
        */
		[calloutActions.CALLOUT_CLOSED]() {},

		/**
        * Dispatches when the callout footer action clicked
        * @actionType SN_HELP_CALLOUT#CALLOUT_FOOTER_ACTION_CLICKED
        */
		[calloutActions.CALLOUT_FOOTER_ACTION_CLICKED]() {}
	}
});
