import {actionTypes} from '@servicenow/ui-core';
import {
	coreActions,
	guidanceTypes,
	dataHandlerActions,
	fetchStatus,
	internalActions
} from '../constants';
import {footerActionTypes, snHelpActionTypes} from '../../../../sn-help/constants';
import {
	fetchGuidanceHandler,
	fetchGuidanceFailureHandler,
	fetchGuidanceSuccessHandler,
	setInteractionHandler,
	setInteractionFailureHandler,
	setInteractionSuccessHandler
} from './dataHandlers';
import {observable} from '../../../../sn-help/observable';

const messageQueue = observable.getInstance();

/**
 * This file contains actions which are dispatched by stepOrchestrator itself.
 */
export const stepOrchestratorInternalActionHandlers = {

	[actionTypes.COMPONENT_CONNECTED] ({state, dispatch, updateState}) {
		messageQueue.subscribe(snHelpActionTypes.GUIDANCE_START_REQUESTED, ({action}) => {
			if(state.fetchStatus === fetchStatus.IN_PROGRESS)
				return;

			updateState({
				fetchStatus: fetchStatus.IN_PROGRESS
			});

			dispatch(coreActions.GUIDANCE_STARTED);
			dispatch(dataHandlerActions.FETCH_GUIDANCE, {id:  action.payload.guidanceId});

			messageQueue.trigger(coreActions.GUIDANCE_ID_UPDATED, {id:  action.payload.guidanceId});
		});

		messageQueue.subscribe(snHelpActionTypes.GUIDANCE_STOP_REQUESTED, () => {
			updateState({
				fetchStatus: fetchStatus.CANCELLED,
				steps: [],
				currentStep: null,
				totalSteps: null
			});
			messageQueue.trigger(coreActions.GUIDANCE_ID_UPDATED, {id:  null});
		});
	},

	[actionTypes.COMPONENT_DISCONNECTED] ({updateState}) {
		updateState({
			interactionId: -1,
			fetchStatus: fetchStatus.READY,
			steps: [],
			currentStep: null,
			totalSteps: null,
			answers: {}
		});
	},

	[dataHandlerActions.FETCH_GUIDANCE] : fetchGuidanceHandler,
	[dataHandlerActions.FETCH_GUIDANCE_SUCCEEDED] : fetchGuidanceSuccessHandler,
	[dataHandlerActions.FETCH_GUIDANCE_FAILED] : fetchGuidanceFailureHandler,

	[dataHandlerActions.SET_INTERACTION] : setInteractionHandler,
	[dataHandlerActions.SET_INTERACTION_SUCCEEDED] : setInteractionSuccessHandler,
	[dataHandlerActions.SET_INTERACTION_FAILED] : setInteractionFailureHandler,

	/**
	 * Executes when user clicks close in Modal or Callout.
	 * @param state
	 * @param dispatch
	 * @param updateState
	 * @param properties
	 */
	[internalActions.GUIDANCE_CLOSED]({state, dispatch, updateState, properties}) {
		dispatch(dataHandlerActions.SET_INTERACTION, {
			id: state.interactionId,
			data: {
				guidance: properties.guidanceId,
				currentStep: {
					step: state.steps[state.currentStep].sys_id,
					action: footerActionTypes.CLOSED,
				}
			},
		});
		dispatch(coreActions.GUIDANCE_CANCELLED);

		updateState({
			interactionId: -1,
			fetchStatus: fetchStatus.READY,
			currentStep: null,
			steps: [],
			totalSteps: null,
			answers: {}
		});
	},

	/**
	 * Executes when user clicks any footer action in Modal or Callout. Will dispatch interaction action and updates
	 * the state
	 * @param state
	 * @param action
	 * @param dispatch
	 * @param updateState
	 * @param properties
	 */
	[internalActions.GUIDANCE_FOOTER_ACTION_CLICKED] ({state, action, dispatch, updateState, properties}) {
		const {payload} = action;
		const userAction = payload.action && payload.action.value;
		let newState = {};

		if(userAction === footerActionTypes.FINISH) {
			dispatch(coreActions.GUIDANCE_ENDED);
			newState = {
				interactionId: -1,
				currentStep : null,
				steps: [],
				totalSteps: null,
				fetchStatus: fetchStatus.READY,
				answers: {}
			};
		} else if(userAction === footerActionTypes.NEXT) {
			dispatch(coreActions.GUIDANCE_STEP_ACTION_TAKEN);
			newState = {
				currentStep : state.currentStep + 1,
				answers: {}
			};
		} else if(userAction === footerActionTypes.PREVIOUS) {
			dispatch(coreActions.GUIDANCE_STEP_ACTION_TAKEN);
			newState = {
				currentStep : state.currentStep - 1,
				answers: {}
			};
		}

		const interactionPayload = {
			id: state.interactionId,
			data: {
				guidance: properties.guidanceId,
				currentStep: {
					step: state.steps[state.currentStep].sys_id,
					action: userAction,
					isLastStep: state.currentStep === state.steps.length-1
				}
			},
		};
		if(state.guidanceType === guidanceTypes.MODAL) {
			interactionPayload.data.answers = Object.keys(state.answers).map(quesId => ({
				id: quesId,
				values: state.answers[quesId]
			}));
		}

		dispatch(dataHandlerActions.SET_INTERACTION, interactionPayload);
		updateState(newState);
	}
};
