import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {modalActionHandlers} from './actionHandlers';
import {modalActions} from './constants';
import styles from './sn-help-modal.scss';
import {view} from './view.js';

/**
 *	SN-Help-Modal Component
 *
 * 	A wrapper on now-modal component
 *  Based on the specific layout and contentType SN-HELP-MODAL will show the suitable modal
 *	@summary Provides guidance, one kind of guidance step in sn-help
 *	@seismicElement sn-help-modal
 */
createCustomElement('sn-help-modal', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	properties: {

		/**
		 * An array of objects that defines the buttons in the footer of the sn-help-modal.
		 * You can define as many as four buttons. The buttons are laid out
		 * from right to left.
		 *
		 * The keys of each object are properties for `now-button`. See `now-button`
		 * for more documentation on these properties.
		 * @type {{ label: string, variant: string, disabled: boolean}[]}
		 */
		footerActions: {
			schema: {
				type: 'array',
				items: {
					type: 'object',
					properties: {
						label: {type: 'string'},
						variant: {type: 'string'},
						disabled: {type: 'boolean'},
						clickActionType: {type: 'string'}
					}
				}
			}
		},
		/**
    * specify layout for modal whether its TEXT,IMAGE,VIDEO,TEXT_IMAGE
    * @type {string}
    */
		layout: {
			default: 'TEXT',
			type: 'string'
		},
		/**
    * specify content properties to render a specific content
    * @type Object.<video_link: string, image: string, questions: Array.<sys_id:string,type:string,variant:string,answers:Array.<{label:string,value:string}>,
    * question:string, mandatory:boolean>, url: string, html: string, custom_html: string, image_alt: string, video_alt: string>
    */
		content: {
			default: {
				image: '',
				questions: [{
					id: '',
					type: '',
					variant: '',
					answers: [],
					question: '',
					mandatory: false
				}],
				url: '',
				html: '',
				custom_html: '',
				title: '',
				subtitle: '',
				footer: '',
				image_link: '',
				video_link: '',
				image_alt: '',
				video_alt: ''
			},
			schema: {
				type: 'object',
				properties: {
					subtitle: { type: 'string' },
					footer: { type: 'string' },
					video_link: { type: 'string' },
					image: { type: 'string' },
					image_link: { type: 'string' },
					questions: { type: 'array' },
					url: { type: 'string' },
					html: { type: 'string' },
					custom_html: { type: 'string' },
					image_alt: { type: 'string' },
					video_alt: { type: 'string' }
				}
			}
		},
		/**
        *  Specify showStepProgress config
        * @type Object.<showStepProgress:boolean, totalSteps:number, currentStep:number>
        *  totalSteps and currentStep are required when showStepProgress is true
        */
		stepProgressConfig: {
			default: {
				showStepProgress: false,
			},
			schema: {
				type: 'object',
				properties: {
					showStepProgress: {type: 'boolean'},
					totalSteps: {type: 'number'},
					currentStep: {type: 'number'}
				}
			}
		}
	},
	actionHandlers: modalActionHandlers,
	dispatches: {
		/**
    * Dispatches when the modal is closed
    * @actionType SN_HELP_MODAL#MODAL_CLOSED
    */
		[modalActions.MODAL_CLOSED]() {},

		/**
    * Dispatches when the modal footer action clicked
    * @actionType SN_HELP_MODAL#MODAL_FOOTER_ACTION_CLICKED
    */
		[modalActions.MODAL_FOOTER_ACTION_CLICKED]() {}
	}
});
