import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-icon';
import styles from './sn-help-step-progress.scss';

const view = state => {
	const { totalSteps, currentStep } = state.properties;

	if(!totalSteps || !currentStep || currentStep < 1 || totalSteps <= 1)
		return;


	const stepProgressView = [];
	for(let i=1; i<=totalSteps; i++)
		stepProgressView.push(<now-icon-presence presence={currentStep >=i ? 'available' : 'offline'} size="md"/>);

	return stepProgressView;
};

/**
 * 	Sn Help Step Progress Component
 *
 * 	@summary A functional component which displays the progress of guidance using NDS Icon presence component.
 *	@seismicElement sn-help-step-progress
 */
createCustomElement('sn-help-step-progress', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	properties: {
		/**
		 * 	Total no of steps present in progress
		 */
		totalSteps: {
			required: true,
		},
		/**
		 * 	The step number till which progress is completed
		 */
		currentStep: {
			required: true
		}
	}
});
