import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {snHelpQuestionView} from './view';
import {questionActionTypes, questionVariants, questionTypes} from './constants';
import {questionActionHandlers} from './actionHandlers';
import styles from './sn-help-question.scss';

/**
 * 	@summary Renders question with choices based on props. Supports radio, checkbox, dropdown and text
 *	@seismic sn-help-question
 */
createCustomElement('sn-help-question', {
	renderer: {
		type: snabbdom,
		view: snHelpQuestionView
	},
	styles,
	initialState: {
		/**
		 *	Stores user selected options. Radio and Dropdown returns aggregated answers.
		 *	Checkbox uses this to store options.
		 */
		selectedOptions: []
	},
	properties: {
		/**
		 *	An array of choices in which user can select single/multiple choices
		 */
		answers: {
			default: [],
			required: false,
			schema: {
				type: 'array',
				answers: {
					type: 'object',
					properties: {
						label: {type: 'string'},
						value: {type: 'string'},
						id: {type: 'string'}
					}
				}
			}
		},

		/**
		 *	Unique identifier or name to distinguish question
		 */
		id: {
			required: true,
			schema: {
				type: 'string'
			}
		},

		/**
		 *	The question which needs to be answered by user
		 */
		question: {
			required: true,
			schema: {
				type: 'string'
			}
		},

		/**
		 *	Decides the type of the question.User can select single or multiple choices
		 */
		type: {
			default: 'SINGLE',
			required: false,
			schema: {
				type: 'string',
				enum: [questionTypes.SINGLE, questionTypes.MULTIPLE]
			}
		},

		/**
		 *	If true, user must answer this question.Used to do basic validation
		 */
		mandatory: {
			default: false,
			required: false,
			schema: {type: 'string'}
		},

		/**
		 *	Variant of the question. It can be Radio,Dropdown or Checkbox. If not mentioned,
		 *	the default variant will be derived at run time based on choices
		 */
		variant: {
			required: false,
			schema: {
				type: 'string',
				enum: [questionVariants.CHECKBOX, questionVariants.RADIO, questionVariants.DROPDOWN, questionVariants.TEXT]
			}
		},
	},
	actionHandlers: questionActionHandlers,
	dispatches: {
		/**
		 *	Dispatched when user selects/type any choice/value.
		 *	@type {{id: string, values: array}}
		 *	@valueType {{name: string, value: string}}
		 *	@action	SN_HELP_QUESTION#VALUES_SET
		 */
		[questionActionTypes.QUESTION_VALUES_SET]: {}
	}
});
