import {coreActions} from '../../components/connected/sn-help-step-orchestrator';
import {snHelpActionTypes} from '../constants';

export const guidanceActionHandlers = {
	/**
	 * Guidance Actions
	 */

	[coreActions.GUIDANCE_STARTED]({action, dispatch}) {
		action.stopPropagation();
		dispatch(snHelpActionTypes.GUIDANCE_STARTED);
	},

	[coreActions.GUIDANCE_ENDED]({action, dispatch}) {
		action.stopPropagation();
		dispatch(snHelpActionTypes.GUIDANCE_ENDED);
	},

	[coreActions.GUIDANCE_CANCELLED]({action, dispatch}) {
		action.stopPropagation();
		dispatch(snHelpActionTypes.GUIDANCE_CANCELLED);
	},

	/**
	 * Guidance Step Actions
	 */

	[coreActions.GUIDANCE_STEP_STARTED]({action, dispatch}) {
		action.stopPropagation();
		dispatch(snHelpActionTypes.GUIDANCE_STEP_STARTED);
	},

	[coreActions.GUIDANCE_STEP_ENDED]({action, dispatch}) {
		action.stopPropagation();
		dispatch(snHelpActionTypes.GUIDANCE_STEP_ENDED);
	},

	[coreActions.GUIDANCE_STEP_ACTION_TAKEN]({action, dispatch}) {
		action.stopPropagation();
		dispatch(snHelpActionTypes.GUIDANCE_STEP_ACTION_TAKEN);
	},
};
