import {actionTypes} from '@servicenow/ui-core';
import {observable} from '../../../sn-help/observable';
import {coreActions} from '../../connected/sn-help-step-orchestrator';
import {snHelpActionTypes} from '../../../sn-help';

const messageQueue = observable.getInstance();

export const snHelpDataProviderActionHandlers = {
	[actionTypes.COMPONENT_CONNECTED]({properties, updateProperties}) {
		messageQueue.subscribe(coreActions.GUIDANCE_ID_UPDATED, ({id})=> {
			updateProperties({
				guidanceId: id
			});
		});

		messageQueue.subscribe(snHelpActionTypes.GUIDANCE_ADD_NODE_REQUESTED, ({action}) => {
			const {id, ref} = action.payload;
			const {guidanceElemRegistry} = properties;

			guidanceElemRegistry.set(id, ref.current);

			updateProperties({
				guidanceElemRegistry
			});
		});

		messageQueue.subscribe(snHelpActionTypes.GUIDANCE_REMOVE_NODE_REQUESTED, ({action}) => {
			const {guidanceElemRegistry} = properties;

			guidanceElemRegistry.delete(action.payload.id);

			updateProperties({
				guidanceElemRegistry
			});
		});

		messageQueue.subscribe(snHelpActionTypes.GUIDANCE_CLEAR_NODES_REQUESTED, () => {
			const {guidanceElemRegistry} = properties;

			guidanceElemRegistry.clear();

			updateProperties({
				guidanceElemRegistry
			});
		});
	},

	[actionTypes.COMPONENT_DISCONNECTED]() {
		messageQueue.unsubscribe(coreActions.GUIDANCE_ID_UPDATED);
	},
};
