import {customRestEffect} from '../../../../effects/customRestEffect';
import {
	dataHandlerActions,
	fetchStatus
} from '../constants';

const {
	FETCH_GUIDANCE_SUCCEEDED,
	FETCH_GUIDANCE_FAILED,

	SET_INTERACTION_SUCCEEDED,
	SET_INTERACTION_FAILED
} = dataHandlerActions;

export const fetchGuidanceHandler = customRestEffect('/api/now/help/guidance/:id', {
	method: 'GET',
	batch: false,
	pathParams: ['id'],

	successActionType: FETCH_GUIDANCE_SUCCEEDED,
	errorActionType: FETCH_GUIDANCE_FAILED
});

export const fetchGuidanceSuccessHandler = ({updateState, action}) => {
	const {steps, type} = action.payload.result.data;
	updateState({
		interactionId: -1,
		steps,
		currentStep: 0,
		guidanceType: type,
		fetchStatus: fetchStatus.DONE
	});
};

export const fetchGuidanceFailureHandler = ({updateState}) => {
	updateState({
		steps: null,
		currentStep: null,
		interactionId: null,
		fetchStatus: fetchStatus.FAILED
	});
};


export const setInteractionHandler = customRestEffect('/api/now/help/interactions/:id', {
	method: 'POST',
	batch: false,
	pathParams: ['id'],
	dataParam: 'data',

	successActionType: SET_INTERACTION_SUCCEEDED,
	errorActionType: SET_INTERACTION_FAILED
});

export const setInteractionSuccessHandler = ({updateState, action}) => {
	updateState({interactionId: action.payload.result.sys_id});
};

export const setInteractionFailureHandler = () => {};
