import '@servicenow/now-checkbox';
import '@servicenow/now-dropdown';
import '@servicenow/now-input';
import '@servicenow/now-radio-buttons';
import '@servicenow/now-icon';
import {questionVariants, questionTypes, MAX_CHOICES_WITHOUT_DROPDOWN} from './constants';

export const snHelpQuestionView = state => {
	const {question, id, mandatory} = state.properties;

	if(!question || !id)
		return;

	const answerVariant = renderVariant(state);
	if(!answerVariant)
		return;

	//Render question only if answer variant is present
	return (
		<div className={'question-root'}>
			<p className={'question-text'}>{question}{mandatory === 'true' ? <now-icon className="required-indicator" icon={'asterisk-fill'} size={'sm'}/> : null}</p>
			{answerVariant}
		</div>
	);
};

const renderVariant = state => {
	const {variant, answers, type} = state.properties;
	const customVariant = getVariant(variant, answers.length, type);

	// Multiple type in Radio variant is not supported
	if(customVariant === questionVariants.RADIO && type === questionTypes.MULTIPLE)
		return;

	let answerVariant;
	switch (customVariant) {
		case questionVariants.CHECKBOX: answerVariant = checkboxView(state); break;
		case questionVariants.DROPDOWN: answerVariant = dropdownView(state); break;
		case questionVariants.TEXT: answerVariant = inputView(state); break;
		case questionVariants.RADIO: answerVariant = radioView(state); break;
		default: answerVariant = inputView(state); break;
	}
	return answerVariant;
};

const getVariant = (userVariant, choicesLength, type) => {
	if(userVariant)
		return userVariant;


	if(choicesLength === 0)
		return questionVariants.TEXT;


	if(choicesLength > MAX_CHOICES_WITHOUT_DROPDOWN)
		return questionVariants.DROPDOWN;


	if(type === questionTypes.SINGLE)
		return questionVariants.RADIO;

	return questionVariants.CHECKBOX;
};

const checkboxView = state => {
	const {answers} = state.properties;

	const checkboxes = answers.map(answer => <now-checkbox className={'question-checkbox'} label={answer.label} name={answer.sys_id}/>);

	return (
		<div className={'question-checkboxes'}>
			{checkboxes}
		</div>
	);
};

const dropdownView = state => {
	const {answers, id, type} = state.properties;

	return <now-dropdown bare={true} placeholder="Select" items={answers} name={id} select={type === questionTypes.SINGLE ? 'single' : 'multi'}/>;
};

const inputView = state => <now-input className={'question-input'} name={state.properties.id}/>;

const radioView = state => {
	const {answers, id} = state.properties;

	return <now-radio-buttons name={id} options={answers}/>;
};
