import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './sn-help-callout-content.scss';
import '@servicenow/now-button';
import '@servicenow/now-rich-text';
import {calloutActions} from './constants';

//render footer in sn-help-callout-content
const renderFooter = (footerActions, dispatch) => footerActions ?
	<div className="sn-help-callout-footer">
		{renderFooterActions(footerActions, dispatch)}
	</div> : null;

//render footer-buttons in sn-help-callout-content
//here footerActionsObj.slice(0, 2) make sure that footer should not have more than two buttons
const renderFooterActions = (footerActions, dispatch) =>  footerActions.slice(0, 2)
	.map(footerAction => {
		const {variant, label, disabled, clickActionType} = footerAction;
		const eventInfo = {
			type: clickActionType || calloutActions.CALLOUT_FOOTER_ACTION_CLICKED,
			payload: {
				action: footerAction
			}
		};
		const actionProps = {
			className: 'sn-help-callout-footer-button',
			variant,
			label,
			disabled,
			'on-click': () => {
				disabled ? '' : dispatch(() => eventInfo);
			}
		};

		return <now-button {...actionProps} size='sm'/>;
	})
	.reverse();


const view = ({properties}, dispatch) => {
	const {content, footerActions, heading} = properties;
	const eventInfo = {
		type: calloutActions.CALLOUT_CLOSED,
		payload: {value: false}
	};
	return (
		<div className='sn-help-callout-content'>
			<div className='sn-help-callout-header'>
				<h1 className='sn-help-callout-heading'>
					<span className='sn-help-callout-heading-label'>{heading}</span>
				</h1>
				<now-button-iconic icon="close-fill" variant="tertiary" size="sm" bare on-click={() => dispatch(() => eventInfo)}/>
			</div>
			<div className='sn-help-callout-content-body'>
				<now-rich-text html={content}/>
			</div>
			{ renderFooter(footerActions, dispatch) }
		</div>
	);
};

createCustomElement('sn-help-callout-content', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		content: {type: 'string'},
		footerActions: {type: 'array'},
		heading: {type: 'string'}
	},
	styles
});
