import {createCustomElement, actionTypes} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './sn-help.scss';
import {view} from './view';
import {guidanceActionHandlers} from './actions/guidance';
import {snHelpActionTypes} from './constants';

/**
 *	Help Component
 *
 * 	Supports help panel, guidance, tour. Embed the help panel
 * 	component into your code just like shown in examples. The actions mentioned
 * 	in dispatches section can be listened through behaviors. For more information
 * 	on behaviors please refer to behaviors section in emotive portal
 *
 *	@summary Provides help panel, guidance, tour
 *	@seismicElement sn-help
 */
createCustomElement('sn-help', {
	renderer: {
		type: snabbdom,
		view
	},
	styles,
	properties: {},
	actionHandlers: {
		/**
		 *	Executes when component is connected to DOM
		 */
		[actionTypes.COMPONENT_CONNECTED]() {},

		/**
		 *	Executes when component is removed from DOM
		 */
		[actionTypes.COMPONENT_DISCONNECTED]() {},

		/**
		 *	Executes when component is rendered
		 */
		[actionTypes.COMPONENT_RENDERED]() {},
		...guidanceActionHandlers
	},
	dispatches: {
		/**
		 * 	GUIDANCE ACTIONS
		 */

		/**
		 *	Dispatches when the guidance started
		 *	@actionType SN_HELP#GUIDANCE_STARTED
		 */
		[snHelpActionTypes.GUIDANCE_STARTED]: {},

		/**
		 *	Dispatches when the guidance ended
		 *	@actionType SN_HELP#GUIDANCE_ENDED
		 */
		[snHelpActionTypes.GUIDANCE_ENDED]: {},

		/**
		 *	Dispatches when the guidance cancelled
		 *	@actionType SN_HELP#GUIDANCE_CANCELLED
		 */
		[snHelpActionTypes.GUIDANCE_CANCELLED]: {},

		/**
		 *	Dispatches when the guidance step started
		 *	@actionType SN_HELP#GUIDANCE_STEP_STARTED
		 */
		[snHelpActionTypes.GUIDANCE_STEP_STARTED]: {},

		/**
		 *	Dispatches when the guidance step ended
		 *	@actionType SN_HELP#GUIDANCE_STEP_ENDED
		 */
		[snHelpActionTypes.GUIDANCE_STEP_ENDED]: {}
	}
});
