export const observable = (function () {
	let instance;

	function init() {
		const handlers = new Map();
		return {
			/**
			 * Subscribes the handler to given name
			 * @param name
			 * @param handler
			 */
			subscribe(name, handler) {
				let actionHandlers = handlers.get(name);
				if(!actionHandlers)
					actionHandlers = [];

				actionHandlers.push(handler);
				handlers.set(name, actionHandlers);
			},

			/**
			 * Unsubscribe handlers of given name
			 * @param name
			 */
			unsubscribe(name) {
				delete handlers.delete(name);
			},

			/**
			 * Triggers all handlers of given name with payload
			 * @param name
			 * @param payload
			 */
			trigger(name, payload) {
				setTimeout(()=>{
					handlers.get(name).forEach( handler => handler(payload) );
				});
			}
		};
	}

	return {
		/**
		 * Use this function to load observable in your files. Makes sure there will be only one instance of
		 * observable(Singleton).
		 * @returns {{subscribe(*, *=): void, unsubscribe(*): void, trigger(*, *=): void}}
		 */
		getInstance () {
			if (!instance)
				instance = init();

			return instance;
		}
	};
})();
