import '@servicenow/now-popover';
import './sn-help-callout-content';

export const view = state => {
	const {targetRef, footerActions, content, heading} = state.properties;
	return <now-popover positionTarget={targetRef} opened={state.opened} manageOpened={true}>
		<sn-help-callout-content slot="content" footerActions={footerActions} content={content} heading={heading}/>
	</now-popover>;
};
