import {questionActionTypes} from './constants';

export const questionActionHandlers = {
	/**
	 *	Checkbox actions
	 */

	/**
	 *	Executes when user select/unselect any checkbox. Value in payload is a boolean which informs the user action
	 *	@param action
	 * 	@param dispatch
	 * 	@param properties
	 * 	@param state
	 * 	@param updateProperties
	 */
	'NOW_CHECKBOX#CHECKED_SET': ({action, dispatch, properties, state, updateState}) => {
		action.stopPropagation();
		let {selectedOptions} = state;

		if (action.payload.value)
			selectedOptions = [...selectedOptions, action.payload.name];
		else
			selectedOptions.splice(selectedOptions.indexOf(action.payload.name),1);

		updateState({selectedOptions, shouldRender: false});
		dispatch(questionActionTypes.QUESTION_VALUES_SET, {
			id: properties.id,
			values: selectedOptions
		});
	},


	/**
	 *	Dropdown actions
	 */

	/**
	 * 	Executes when user select/unselect any choice in dropdown. Value in payload is an array of choices user
	 * 	selected
	 *	@param action
	 * 	@param dispatch
	 * 	@param properties
	 */
	'NOW_DROPDOWN#SELECTED_ITEMS_SET': ({action, dispatch, properties}) => {
		action.stopPropagation();
		dispatch(questionActionTypes.QUESTION_VALUES_SET, {
			id: properties.id,
			values: action.payload
		});
	},

	/**
	 * 	Executes when user opens/closes dropdown. Restricting NDS actions to stop propagating further.
	 *	@param action
	 */
	'NOW_DROPDOWN#OPENED_SET': ({action}) => action.stopPropagation(),

	/**
	 * 	Executes when user select/unselect any choice in dropdown. Restricting NDS actions to stop propagating further.
	 *	@param action
	 */
	'NOW_DROPDOWN#ITEM_CLICKED': ({action}) => action.stopPropagation(),


	/**
	 *	Input actions
	 */

	/**
	 *	Executes on blur event of input. NDS Input also dispatches 'NOW_INPUT#INVALID_SET' but we are using
	 *	only 'NOW_INPUT#VALUE_SET'.This event will be dispatched only if input is neither disabled nor in readonly mode.
	 * 	@param action
	 * 	@param dispatch
	 * 	@param properties
	 */
	'NOW_INPUT#VALUE_SET': ({action, dispatch, properties}) => {
		action.stopPropagation();
		dispatch(questionActionTypes.QUESTION_VALUES_SET, {
			id: properties.id,
			values: [action.payload]
		});
	},

	/**
	 * 	Executes on blur event of input. Restricting NDS actions to stop propagating further.
	 *	@param action
	 */
	'NOW_INPUT#INVALID_SET': ({action}) => action.stopPropagation(),


	/**
	 *	Radio actions
	 */

	/**
	 *	Executes when user select/unselect any radio. Value in payload informs the user selected choice
	 *	@param action
	 * 	@param dispatch
	 * 	@param properties
	 */
	'NOW_RADIO_BUTTONS#VALUE_SET': ({action, dispatch, properties}) => {
		action.stopPropagation();
		dispatch(questionActionTypes.QUESTION_VALUES_SET, {
			id: properties.id,
			values: [action.payload]
		});
	}
};
