import {modalActions} from './constants';

export const modalActionHandlers = {

	'NOW_MODAL#OPENED_SET': ({updateProperties, action, dispatch}) => {
		updateProperties({opened: false});
		action.stopPropagation();
		dispatch(modalActions.MODAL_CLOSED, action.payload);
	},

	'NOW_MODAL#FOOTER_ACTION_CLICKED' : ({action, dispatch}) => {
		action.stopPropagation();
		dispatch(modalActions.MODAL_FOOTER_ACTION_CLICKED, action.payload);
	},

	/**
	 * Restricting NDS actions to stop propagating further. Getting executed along with
	 * 'NOW_MODAL#FOOTER_ACTION_CLICKED'
	 * @param action
	 * @returns {*}
	 */
	'NOW_BUTTON#CLICKED': ({action}) => action.stopPropagation()

};
